// typography start

html {
  font-size: 62.5%;
}
body {
  font-size: 1.6rme;
  font-family: Inter, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
p {
  color: $body-text;
}
h1,
h2,
h3 {
  color: #fff;
  margin: 0;
}
h1,
h2 {
  font-size: 3rem;
  text-shadow: 0 0 30px #0000005a;
}

// typography end
// reset end
